import React, { Component, PropTypes } from 'react';
import { connect } from 'react-redux';
import { browserHistory } from 'react-router';
import Step2 from '../../components/Step2';
import { getTotal, getClothes } from './orderReducer';
import { trackEvent } from '../../services/analytics';

class Step2Container extends Component {
  constructor() {
    super();
    this.state = {};
    this.openCategory = this.openCategory.bind(this);
    this.setChoicesCloth = this.setChoicesCloth.bind(this);
    this.goNext = this.goNext.bind(this);
    this.deleteItem = this.deleteItem.bind(this);
    this.toggleOrSetAlteration = this.toggleOrSetAlteration.bind(this);
  }

  componentDidMount() {
    window.scrollTo(0, 0);
    if (window.location.hostname !== 'localhost') {
      window.onbeforeunload = function () {
        return true;
      };
    }
  }

  setChoicesCloth(cloth, index) {
    window.scrollTo(0, 0);
    if (cloth) {
      trackEvent('ouvrir-retouches', cloth.slug);
    }
    this.setState({ showChoicesCloth: cloth, showChoicesItemIndex: index });
  }

  openCategory(openedCategory) {
    trackEvent('ouvrir-categorie', openedCategory);
    this.setState({ openedCategory });
  }

  toggleOrSetAlteration(itemIndex, alterationId, quantity) {
    trackEvent('toggle-retouche', alterationId);
    if (typeof quantity === 'number') {
      this.props.dispatch({ type: 'ORDER.SET_ALTERATION_QUANITY', itemIndex, alterationId, quantity });
    } else {
      this.props.dispatch({ type: 'ORDER.TOGGLE_ALTERATION', itemIndex, alterationId });
    }
  }

  deleteItem(itemId, slug) {
    trackEvent('enlever-panier-etape2', slug);
    this.props.dispatch({ type: 'ORDER.DELETE_ITEM', itemId });
  }

  goNext() {
    if (this.isOrderValid()) {
      browserHistory.push('/order/step3');
    } else {
      trackEvent('erreur', 'retouches-incompletes');
      this.setState({ hasError: true });
    }
  }

  isOrderValid() {
    return this.props.clothes && this.props.clothes.length > 0
      && !this.props.clothes.find(cloth => !cloth.alterations || cloth.alterations.length === 0);
  }

  render() {
    const isButtonValid = this.isOrderValid();
    return (
      <Step2
        clothes={this.props.clothes}
        total={this.props.total}
        toggleOrSetAlteration={this.toggleOrSetAlteration}
        showChoicesCloth={this.state.showChoicesCloth}
        showChoicesItemIndex={this.state.showChoicesItemIndex}
        openedCategory={this.state.openedCategory}
        openCategory={this.openCategory}
        close={() => this.setChoicesCloth()}
        setChoicesCloth={this.setChoicesCloth}
        isButtonValid={isButtonValid}
        goNext={this.goNext}
        hasError={this.state.hasError}
        deleteCloth={this.deleteItem}
      />
    );
  }
}

Step2Container.propTypes = {
  clothes: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
  dispatch: PropTypes.func.isRequired,
  total: PropTypes.number.isRequired,
  openedCategory: PropTypes.shape({}),
  showChoicesCloth: PropTypes.shape({}),
};

Step2Container.defaultProps = {
  openedCategory: undefined,
  showChoicesCloth: undefined,
};

const mapStateToProps = state => ({
  clothes: getClothes(state),
  total: getTotal(state),
  openedCategory: state.order.openedCategory,
});

const mapDispatchToProps = dispatch => ({ dispatch });

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Step2Container);



