import React, { PropTypes } from 'react';
import styled from 'styled-components';
import { Categories, Category, Header3, Center, Button, Choice, Price, colors, mobileThresholdPixels }
  from './styledComponents';

const Title4 = styled.h4`
  padding: 0 20px;
  @media (max-width: ${mobileThresholdPixels}) {
    margin: 5px 10px;
    padding: 0px;
  }
`;

const Add = styled.span`
  background-color: white;
  color: ${colors.navy};
  border-radius: 50%;
  border: 1.5px ${colors.navy} solid;
  padding: 0px 5px;
  margin: 0px 5px;
`;

function renderItem(item, toggleOrSetAlteration, cloth) {
  const quantity = (cloth.alterations && cloth.alterations[item.id]) || 0;
  return (
    <Choice
      isSelected={quantity}
      onClick={() => !(quantity > 0 && item.multiple) && toggleOrSetAlteration(item.id)}
      key={item.id}
    >
      {item.name}
      {item.multiple && <div>
        {' | '}
        {quantity > 0 && <Add onClick={() => toggleOrSetAlteration(item.id, quantity - 1)}>-</Add>}
        <span>Qté: {quantity}</span>
        {quantity > 0 && <Add onClick={() => toggleOrSetAlteration(item.id, quantity + 1)}>+</Add>}
        {' | '}
      </div>}
      <Price>{item.price}€</Price>
    </Choice>
  );
}

const Alterations = ({ clothRef, openedCategory, openCategory, close, toggleOrSetAlteration, cloth }) => (
  <Categories>
    {clothRef.categories.map(category =>
      <div key={category.name}>
        <Category onClick={() => openCategory(category.name)}>
          <Header3>
            <Title4>
              {openedCategory === category.name ?
                <img src="https://icon.now.sh/chevron/up/12" alt="chevron icon" />
                : <img src="https://icon.now.sh/chevron/down/12" alt="chevron icon" />
              }
              {` ${category.name}`}
            </Title4>
          </Header3>
        </Category>
        {openedCategory === category.name &&
          category.items
          .filter(item => item && !item.hidden)
          .map(item => renderItem(item, toggleOrSetAlteration, cloth))}
      </div>,
    )}
    <Center>
      <Button onClick={close}>Valider ce vêtement</Button>
    </Center>
  </Categories>
);

Alterations.propTypes = {
  clothRef: PropTypes.shape({}).isRequired,
  openCategory: PropTypes.func.isRequired,
  openedCategory: PropTypes.string,
  close: PropTypes.func.isRequired,
  toggleOrSetAlteration: PropTypes.func.isRequired,
  cloth: PropTypes.shape({}).isRequired,
};

export default Alterations;



